body {
  background-color: #222E35;
  margin: 0;
  font-family: 'Segoe UI', Helvetica Neue, Helvetica, Lucida Grande, Arial;
}
.app-window {
  display: flex;
  height: 100vh;
  background-color: #202C33;
}
.sidebar {
  width: 35%;
  max-width: 415px;
  display: flex;
  flex-direction: column;
  border-right: 1px solid #2F3B43;
  transition: all ease 0.3s;
}

header {
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
}
.header--avatar {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  cursor: pointer;
}
.header--buttons {
  display: flex;
}
.header--btn {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.search {
  background-color: #111B21;
  border-bottom: 1px solid #8696a026;
  padding: 5px 15px;
}

.search--input {
  background-color: #202C33;
  height: 40px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  padding: 0 10px;
}

.search--input input {
  flex: 1;
  border: 0;
  outline: 0;
  background-color: transparent;
  margin-left: 10px;
  color: #d1d7db;
}
.chatlist {
  flex: 1;
  background-color: #111B21;
  overflow-y: auto;
}

.chatlist::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

.chatlist::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
}

.contentarea {
  flex: 1;
}
.disabled {
  flex: 0;
  width: 0;
  padding: 0;
  margin: 0;
  overflow: hidden;
}

@media (max-width: 1000px) {
  .sidebar {
    width: 40%;
    max-width: 100%;
  }
}
@media (max-width: 865px) {
  .sidebar {
    width: 45%;
  }
}
@media (max-width: 750px) {
  .sidebar {
    width: 100%;
  }
}
