.login {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100vh;
}
.loginIntroArea {
  width: 100%;
  background-color: #222E35;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
}

.loginIntro {
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.loginIntro h1 {
  font-size: 32px;
  text-align: center;
  color: #e9edefe0;
  font-weight: normal;
  margin-top: 20px;
}

.loginIntro h2 {
  font-size: 14px;
  text-align: center;
  color: #8696a0;
  font-weight: normal;
  margin-top: 0;
  margin: 0 0 20px 0;
  line-height: 20px;
}

.loginIntroArea button {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1092F3;
  border: 0;
  outline: 0;
  color: #FFF;
  padding: 10px 20px;
  border-radius: 8px;
  cursor: pointer;
  transition: all ease .3s;
}

.loginIntroArea button:hover {
  background-color: #2374E1;
}

.login--icon {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -2px;
  margin-right: 5px;
}

footer p {
  font-size: 14px;
  text-align: center;
  color: #8696a0;
  font-weight: normal;
  margin: 0 0 10px 0;
  line-height: 20px;
}
footer p a{
  font-size: 14px;
  text-align: center;
  color: #8696a0;
  font-weight: normal;
  margin: 0 0 10px 0;
  line-height: 20px;
  text-decoration: none;
}

footer a:hover {
  text-decoration: underline;
}

@media (max-width: 500px) {
  svg {
    width: 260px;
  }
  .loginIntro h1 {
    font-size: 25px;
  }
}