.newChat {
  width: 35%;
  max-width: 415px;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  background-color: #111B21;;
  display: flex;
  flex-direction: column;
  border: 1px solid #262F34;
  transition: all ease 0.5s;
}
@media (max-width: 750px) {
  .newChat {
    width: 100%;
    max-width: 100%;
  }
}
.newChat--head {
  display: flex;
  background-color: #202C33;
  align-items: center;
  padding: 60px 15px 15px 15px;
}
.newChat--backbutton {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.NewChat--headTitle {
  font-size: 19px;
  height: 40px;
  line-height: 40px;
  flex: 1;
  font-weight: bold;
  color: #e9edef;
  margin-left: 20px;
}
.newChat--list {
  flex: 1;
  overflow-y: auto;

}

.newChat--list::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

.newChat--list::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
}

.NewChat--item {
  display: flex;
  align-items: center;
  height: 70px;
  cursor: pointer;
}

.NewChat--item:hover {
  background-color: #202C33;
}

.newChat--itemavatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin: 0 15px;
}
.newChat--itemArea {
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
  border-bottom: 1px solid #2a3942;
  margin-right: 15px;
}
.newChat--itemname {
  font-size: 17px;
  color: #e9edef;
} 