.chatWindow {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.chatWindow--header {
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.chatWindow--headerinfo {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.chatWindow--avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 0 15px;
}
.chatWindow--name {
  font-size: 17px;
  color: #e9edef;
}
.chatWindow--headerbuttons {
  display: flex;
  align-items: center;
  margin-right: 15px;
}
.chatWindow-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  overflow: hidden;
  transition: all ease 0.2s;
}

.chatWindow--body {
  flex: 1;
  overflow-y: auto;
  background-color: #0B141A;
  /* background-image: url("../../assets/bg-chat-tile-dark_a4be512e7195b6b733d9110b408f075d.png");
  background-size: auto;
  background-position: top; */
  padding: 20px 30px;
}

.chatWindow--body::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

.chatWindow--body::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
}

.chatWindow--footer {
  height: 62px;
  display: flex;
  align-items: center;
}
.chatWindow--pre {
  display: flex;
  margin: 0 15px;
}
.chatWindow--inputarea{
  flex: 1;
}
.chatWindow--input {
  width: 100%;
  height: 40px;
  border: 0;
  outline: 0;
  background-color: #2a3942;
  border-radius: 8px;
  font-size: 15px;
  color: #e9edef;
  padding: 9px 12px 11px;
}
.chatWindow--input::placeholder {
  color: #8696a0;
}
.chatWindow--pos {
  display: flex;
  margin: 0 15px;
}
.chatWindow--emojiarea {
  overflow-y: hidden;
  transition: all ease 0.3s;
}
aside.emoji-picker-react {
  width: 100% !important;
  background: none !important;
}
.emoji-picker-react .emoji-group:before {
  background: none !important;
}