.chatIntroArea {
  background-color: #222E35;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  border-bottom: 6px solid #009588;
}

.chatIntro {
  width: 80%;
  max-width: 560px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.chatIntro h1 {
  font-size: 32px;
  color: #e9edefe0;
  font-weight: normal;
  margin-top: 30px;
}

.chatIntro h2 {
  font-size: 14px;
  text-align: center;
  color: #8696a0;
  font-weight: normal;
  margin-top: 20px;
  line-height: 20px;
}